import React, { useEffect, useState } from "react";
import { Button, Modal } from "antd-mobile";
import "./style.css";
import { Stage } from "../../components/Stage.ts";
import { Hero } from "../../components/Hero.ts";
import { Rect } from "../../components/Rect.ts";
import { Vector } from "../../components/Vector.ts";
import { Platform } from "../../components/Platform.ts";
import { Particle } from "../../components/Particle.ts";
import { PlatformManager } from "../../components/PlatformManager.ts";
import { random, randomOne } from "../../utils.ts";
import {
  addListener,
  setControlType,
} from "../../components/GameController.ts";

let init;
let hero;
let particles: Particle[] = [];
const maxParticleLength = 40;
let particleId = 0;

let current = 0;
const Game = () => {
  const stage = new Stage({
    // 重力加速度
    verticalAcceleration: new Vector(0, 0.6),
    initialHorizontalVelocity: new Vector(-4, 0),
    horizontalAcceleration: new Vector(-0.001, 0),
    onResize: () => {
      init?.();
    },
  });

  let hasIntersect = false;
  let prevHasIntersect = false;

  function isIntersectLeft(hero: Hero, platform: Platform) {
    if (hero.prevPosition.x + hero.width >= platform.prevPosition.x) {
      return false;
    }
    if (hero.prevPosition.y + hero.height >= platform.prevPosition.y) {
      return true;
    }
    const { x, y } = platform.prevPosition;
    const prevRightBottomX = hero.width + hero.prevPosition.x;
    const prevRightBottomY = hero.height + hero.prevPosition.y;
    const tx = (x - prevRightBottomX) / -stage.horizontalVelocity.x;
    const ty = (y - prevRightBottomY) / hero.prevVelocity.y;
    return ty < tx;
  }

  function collideDetect(hero: Hero, platforms: Platform[]) {
    if (hero.position.y > stage.height) {
      // 掉进间隙
      // init();
      gameOver(1, hero);
      return;
    }
    let tempHasIntersect = false;
    for (let i = 0; i < platforms.length; i++) {
      if (Rect.isIntersect(hero, platforms[i])) {
        tempHasIntersect = true;
        const platform = platforms[i];
        hero.velocity = new Vector(0, 0);
        hero.curConJump = 0;

        if (isIntersectLeft(hero, platform)) {
          // init();
          gameOver(2, hero);
          return;
        }
        hero.position.y = platform.position.y - hero.height;

        const particleSize = 8;

        if (current === i) {
          current++;
          if (current > 1) {
            hero.score += i * 10;
          }
          console.log(current, hero);
        }
        if (!prevHasIntersect) {
          for (let i = 0; i < 10; i++) {
            const left = Math.random() > 0.5;
            particles[particleId % maxParticleLength] = new Particle({
              velocity: left
                ? new Vector(random(-4, -2), random(-6, -1))
                : new Vector(random(10, 16), random(-6, -1)),
              mass: 1,
              position: left
                ? new Vector(
                    hero.position.x - particleSize,
                    hero.position.y + hero.height - particleSize
                  )
                : new Vector(
                    hero.position.x + hero.width,
                    hero.position.y + hero.height - particleSize
                  ),
              width: particleSize,
              height: particleSize,
              color: randomOne([hero.color, platform.color]),
            });
            particles[particleId++ % maxParticleLength].applyForce(
              new Vector(0, -2)
            );
          }
        } else {
          particles[particleId % maxParticleLength] = new Particle({
            velocity: new Vector(0, random(-6, -1)),
            mass: 1,
            position: new Vector(
              hero.position.x - particleSize,
              hero.position.y + hero.height - particleSize
            ),
            width: particleSize,
            height: particleSize,
            color: platform.color,
          });
          particles[particleId++ % maxParticleLength].applyForce(
            new Vector(0, -2)
          );
        }
      }
    }
    hasIntersect = tempHasIntersect;
    if (!prevHasIntersect && hasIntersect) {
      stage.horizontalVelocity.add(new Vector(-2, 0));
    }
    if (prevHasIntersect && !hasIntersect) {
      stage.horizontalVelocity.add(new Vector(2, 0));
    }
    prevHasIntersect = hasIntersect;
  }

  const gameOver = async (type: number, hero: Hero) => {
    console.log(type, hero);
    stage.stop();
    const result = await Modal.confirm({
      title: "Game Over",
      content: `得分：${hero.score}`,
      confirmText: "重玩",
    });
    if (result) {
      init();
    } else {
    }
  };

  init = () => {
    stage.reset();
    hero = new Hero({
      position: new Vector(100, 0),
      height: 24,
      width: 24,
      color: "#222f3e",
    });

    const pm = new PlatformManager({
      colors: ["#1dd1a1", "#ff6b6b", "#feca57", "#54a0ff", "#9c88ff"],
    });

    stage.play(() => {
      collideDetect(hero, pm.platforms);
      for (let particle of particles) {
        particle.update(stage);
        particle.draw(stage);
      }
    });

    stage.add(hero, pm);
  };

  document.body.addEventListener("change", function (event: any) {
    setControlType(event.target.value);
  });

  addListener(() => {
    hero.jump();
  });

  init();

  return (
    <div className="game-controller-wrapper">
      <label>
        Keyboard/Touch
        <input
          type="radio"
          checked
          name="radio"
          onChange={() => {}}
          value="keyboard"
        />
      </label>
      <label>
        Audio
        <input type="radio" name="radio" value="audio" />
      </label>
      {/* <Modal
        visible={visible}
        content='人在天边月上明'
        closeOnAction
        onClose={() => {
          setVisible(false)
        }}
        actions={[
          {
            key: 'confirm',
            text: '我知道了',
          },
        ]}
      /> */}
    </div>
  );
};

export default Game;
